    .star {
    z-index: 0;
    border-radius: 5px;
    width: calc(15px);
    width: calc(var(--size));
    height:calc(15px);
    height:calc(var(--size));
    position: absolute;
    background: #262692;
    box-shadow: 0 0 15px #262692;
    border: 2px solid rgb(34, 109, 136);
      
      &.white {
        background: #f2f2f2;
