      }
    }

    .star.near {
        z-index:2;
        border-radius: 7px;
        width: calc(15px + 8px);
        width: calc(var(--size) + 8px);
        height: calc(15px + 8px);
        height: calc(var(--size) + 8px);
      }
      
    .star.far {
      border-radius: 2px;
        z-index: 0;
        width: calc(15px - 6px);
        width: calc(var(--size) - 6px);
        height: calc(15px - 6px);
        height: calc(var(--size) - 6px);
      }
  }
